<template>
  <v-col cols="11" md="4" sm="6">
    <v-card class="py-8 px-5 white black--text">
      <h1 class="text-center">{{ $t('auth.resetPassword.heading') }}</h1>
      <v-card-text>
        <base-alert display="reset-password-page" />
        <v-form ref="form" v-model="valid" @submit.prevent="resetPassword">
          <div>
            <v-text-field
              v-model="user.password"
              color="primary"
              prepend-icon="mdi-lock"
              :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
              light
              :type="showPassword ? 'text' : 'password'"
              :label="$t('auth.resetPassword.form.labels.password')"
              :rules="[rules.required, rules.passwrodMinLength]"
              @click:append="showPassword = !showPassword"
            />
          </div>
          <div>
            <v-text-field
              v-model="confirmPassword"
              color="primary"
              prepend-icon="mdi-lock"
              light
              type="password"
              :label="$t('auth.resetPassword.form.labels.confirmPassword')"
              :rules="[
                rules.required,
                rules.confirmPassword(confirmPassword, user.password)
              ]"
            />
          </div>
          <div>
            <v-btn
              :loading="$store.state.loading.auth.resetPassword"
              color="primary"
              block
              type="submit"
              >{{ $t('auth.resetPassword.button.text') }}</v-btn
            >
          </div>
          <v-divider></v-divider>
        </v-form>
        <div class="text-center black--text mt-4">
          {{ $t('auth.resetPassword.footerText') }}
          <nuxt-link to="/auth/login">{{
            $t('auth.resetPassword.links.goToLogin')
          }}</nuxt-link>
        </div>
      </v-card-text>
    </v-card>
  </v-col>
</template>

<script>
import validations from '@/mixins/validations'
export default {
  mixins: [validations],
  data() {
    return {
      user: {
        password: ''
      },
      confirmPassword: '',
      showPassword: false
    }
  },
  methods: {
    async resetPassword() {
      try {
        this.$refs.form.validate()
        if (this.valid) {
          this.$store.commit('alert/REMOVE_ALERT')
          this.$store.commit('loading/SET_LOADING', {
            option: 'auth.resetPassword',
            value: true
          })
          await this.$axios.post(
            `/api/auth/reset_password/${this.$route.params.token}`,
            {
              password: this.user.password
            }
          )

          this.$store.commit('loading/SET_LOADING', {
            option: 'auth.resetPassword',
            value: false
          })
          const alert = {
            type: 'success',
            display: 'login-page',
            show: true,
            text: 'successMessages.auth.passwordReset'
          }
          this.$store.commit('alert/SET_ALERT', alert)
          this.$router.push('/auth/login')
        }
      } catch (e) {
        const alert = {
          type: 'error',
          display: 'reset-password-page',
          show: true,
          text: e.response ? e.response.data.message : e.message
        }
        this.$store.commit('alert/SET_ALERT', alert)
        this.$store.commit('loading/SET_LOADING', {
          option: 'auth.resetPassword',
          value: false
        })
      }
    }
  }
}
</script>

